<template>
    <div class="container">
        <div class="form-box">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="form.title" placeholder="" style="width: 375px;"></el-input>
                </el-form-item>
                <el-form-item label="" required>
                    <el-select v-model="form.categoryId" style="width: 375px;" placeholder="">
                        <el-option class="search-item-inp" v-for="item in categoryTheList" :key="item.value" :label="item.label" :value="item.value" style="width: 375px;">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <div class="editor-box">
                        <Toolbar style="border-bottom: 1px solid #ccc" :editor="editor" :defaultConfig="toolbarConfig" :mode="mode" />
                        <Editor style="height: 500px; overflow-y: hidden;" v-model="html" :defaultConfig="editorConfig" :mode="mode" @onCreated="onCreated" />
                    </div>
                    <div>*20355,<br>web</div>
                </el-form-item>
                <el-form-item label="">
                    <el-button @click="save" type="primary"></el-button>
                    <el-button @click="dialogVisible = true"></el-button>
                    <el-button @click="back"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-dialog title="" :width="'410px'" :visible.sync="dialogVisible" @close="close">
            <div class="dialog-modal-container">
                <div style="margin-bottom: 20px">{{form.title}}</div>
                <div v-html="html" class="html-box" style=""></div>
            </div>
        </el-dialog>

    </div>
</template>

<script>
import { Editor, Toolbar } from '@wangeditor/editor-for-vue';
import { articleCreate, categoryList, articleDetail, articleUpdate } from '@/api/article';
import dev from '@/utils/dev';

const imgTypeList = ['png', 'jpg', 'jpeg'];
const videoType = ['mp4', 'avi'];

export default {
    components: { Editor, Toolbar },
    data () {
        return {
            form: {
                title: '',
                categoryId: null,
            },
            editor: null,
            html: '',
            toolbarConfig: {},
            editorConfig: {
                placeholder: '...',
                MENU_CONF: {
                    uploadImage: {
                        server: `${dev.URL}/file/upload/cos`,
                        // server: 'http://localhost:5001/imgUpload',
                        headers: {
                            'ysxf-auth-data': this.$store.state.userData,
                            'ysxf-upload-data': JSON.stringify({ category: 'article' })
                        },
                        maxFileSize: 1000 * 1024 * 1024, // 10M
                        //  timeout 
                        onBeforeUpload: file => {
                            let data = null;
                            Object.keys(file).forEach(item => {
                                if (item) data = file[item];
                            });
                            if (data.size > (10 * 1024 * 1024)) {
                                this.errorTip(`10M`);
                                return false;
                            }
                            if (imgTypeList.indexOf(data.extension) == -1) {
                                this.errorTip(`,${imgTypeList.join(',')}`);
                                return false;
                            }
                            return file;

                        },
                        customInsert: (res, insertFn) => {
                            console.log(res.data.id);
                            if (res.code != 0) {
                                this.errorTip('');
                                return;
                            }
                            if (res.code == 0) {
                                this.fileIdList.push(res.data.id);
                                // //  res  url alt href 
                                insertFn(`http://${res.data.url}`, '', '');
                            }
                        }
                    },
                    uploadVideo: {
                        server: `${dev.URL}/file/upload/cos`,
                        // server: 'http://localhost:5001/imgUpload',
                        headers: {
                            'ysxf-auth-data': this.$store.state.userData,
                            'ysxf-upload-data': JSON.stringify({ category: 'article' })
                        },
                        maxFileSize: 1000 * 1024 * 1024, // 10M
                        //  timeout 
                        onBeforeUpload: file => {
                            let data = null;
                            Object.keys(file).forEach(item => {
                                if (item) data = file[item];
                            });
                            if (videoType.indexOf(data.extension) == -1) {
                                this.errorTip(`,${videoType.join(',')}`);
                                return false;
                            }
                            if (data.size > (10 * 1024 * 1024)) {
                                this.errorTip(`10M`);
                                return false;
                            }
                            return file;

                        },
                        customInsert: (res, insertFn) => {
                            console.log(res.data.id);
                            if (res.code != 0) {
                                this.errorTip('');
                                return;
                            }
                            if (res.code == 0) {
                                this.fileIdList.push(res.data.id);
                                // //  res  url alt href 
                                insertFn(`http://${res.data.url}`, '', '');
                            }
                        }
                    }
                }
            },
            mode: 'default', // or 'simple'
            dialogVisible: false,
            categoryTheList: [],
            fileIdList: [],
        };
    },
    computed: {
        userData () {
            return this.$store.state.userData;
        }
    },
    methods: {
        errorTip (str) {
            this.$message.error(str);
        },
        categoryList () {
            categoryList({ isPaging: false }).then(res => {
                if (res.data.code == 0) {
                    this.categoryTheList = res.data.data.rows.map(item => {
                        return { value: item.id, label: item.name };
                    });
                }
            });
        },
        onCreated (editor) {
            this.editor = Object.seal(editor); //  Object.seal() 
        },
        save () {
            if (!this.form.title) {
                this.$message.warning('');
                return;
            }
            if (!this.form.categoryId) {
                this.$message.warning('');
                return;
            }
            if (this.type == 'add') {
                this.createArticle();
            } else {
                this.articleUpdate();
            }
        },
        articleUpdate () {
            const { title, categoryId } = this.form;
            articleUpdate({
                content: this.html,
                fileIdList: this.fileIdList.join(','),
                title,
                categoryId: categoryId,
                id: Number(this.id),
            }).then(res => {
                console.log(res.data);
                if (res.data.code == 0) {
                    this.$message.success('');
                    setTimeout(() => {
                        this.back();
                    }, 2000);
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        createArticle () {
            const { title, categoryId } = this.form;
            articleCreate({
                content: this.html,
                fileIdList: this.fileIdList.join(','),
                title,
                categoryId: categoryId
            }).then(res => {
                console.log(res.data);
                if (res.data.code == 0) {
                    this.$message.success('');
                    setTimeout(() => {
                        this.back();
                    }, 2000);
                } else {
                    this.$message.error(`${res.data.message},${res.data.detail}`);
                }
            });
        },
        back () {
            this.$router.go(-1);
        },
        getDetails () {
            articleDetail({ id: `${this.id}`, categoryId: this.categoryId }).then(res => {
                const { content, title, categoryId } = res.data.data.articleRow;
                this.form.title = title;
                this.html = content;
                this.form.categoryId = categoryId;
            });
        },
        close () {
            this.dialogVisible = false;
            const videos = document.querySelectorAll('video');
            if (videos) {
                videos.forEach(item => {
                    item.pause();
                });
            }
        }
    },
    created () {
        this.categoryList();
        const { type, id, categoryId } = this.$route.query;
        this.type = type;
        if (type == 'edit' && id) {
            this.id = id;
            this.categoryId = categoryId;
            this.getDetails();
        }
    },
